/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
h1,
h2,
p,
a,
img,
ul,
li,
article,
aside,
menu,
nav {
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

body {
  list-style: none;
  font-family: "Roboto", "Times New Roman", sans-serif;
  font-size: 10px;
  line-height: 14px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
